@keyframes bounce {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-15px);
  }
}

@keyframes wobble {
  0%,
  100% {
    transform: rotate(0);
  }
  25% {
    transform: rotate(-5deg);
  }
  75% {
    transform: rotate(5deg);
  }
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease-in-out;
}

.info-modal {
  background: linear-gradient(to bottom, #ffffff, #f0f8ff);
  border-radius: 25px;
  padding: 25px;
  width: 90%;
  max-width: 500px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  text-align: center;
  animation: slideUp 0.4s ease-out, wobble 2s ease-in-out 0.5s;
  border: 8px solid;
  border-style: dashed;
}

/* Header container for icon and title */
.header-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  gap: 15px;
}

.modal-icon {
  font-size: 3rem; /* Reduced from 6rem to fit beside title */
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-icon svg {
  width: 60px; /* Reduced from 100px */
  height: 60px; /* Reduced from 100px */
  filter: drop-shadow(0 4px 6px rgba(0, 0, 0, 0.2));
}

.modal-icon img {
  width: 60px; /* Reduced from 100px */
  height: auto;
  max-height: 60px; /* Reduced from 100px */
  filter: drop-shadow(0 4px 6px rgba(0, 0, 0, 0.2));
}

.info-modal h2 {
  font-size: 2.2rem;
  margin: 0;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.15);
  font-family: "Bubblegum Sans", "Comic Sans MS", cursive, sans-serif;
  letter-spacing: 1px;
  background: linear-gradient(to bottom, #444, #222);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent;
}

.modal-content-with-image {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  background-color: rgba(255, 255, 200, 0.5);
  border-radius: 20px;
  padding: 20px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.modal-image-container {
  width: 100%;
  max-width: 300px;
  margin-bottom: 20px;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.25);
  border: 5px solid white;
}

.modal-image {
  width: 100%;
  height: auto;
  display: block;
  transition: transform 0.3s ease;
}

.modal-image:hover {
  transform: scale(1.05);
}

.modal-text {
  font-size: 1.5rem;
  color: #222;
  line-height: 1.7;
  text-align: center;
  font-family: "Quicksand", "Comic Sans MS", Arial, sans-serif;
  font-weight: 600;
  padding: 0.5rem;
  letter-spacing: 0.5px;
  text-shadow: 1px 1px 1px rgba(255, 255, 255, 0.8);
}

.close-btn {
  padding: 15px 30px;
  border-radius: 50px;
  font-size: 1.4rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  background: linear-gradient(to bottom, #ff9900, #ff6600);
  color: white;
  font-family: "Quicksand", "Comic Sans MS", Arial, sans-serif;
  letter-spacing: 1px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.close-btn:hover {
  background: linear-gradient(to bottom, #ffaa00, #ff7700);
  transform: scale(1.1);
  box-shadow: 0 6px 15px rgba(255, 153, 0, 0.4);
}

.info-modal[style*="border-color: #ffc107"] h2 {
  color: #e0a800;
}

.info-modal[style*="border-color: #17a2b8"] h2 {
  color: #138496;
}

/* Add font imports for better typography */
@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@500;600;700&family=Bubblegum+Sans&display=swap");

@media (max-width: 600px) {
  .info-modal {
    padding: 20px;
    width: 85%;
  }

  .header-container {
    gap: 10px;
    flex-wrap: wrap;
  }

  .info-modal h2 {
    font-size: 1.7rem;
  }

  .modal-icon svg,
  .modal-icon img {
    width: 50px;
    height: 50px;
  }

  .modal-text {
    font-size: 1.3rem;
  }

  .close-btn {
    width: 100%;
    padding: 12px;
    font-size: 1.3rem;
  }
}
